:root {
  --LIGHTGREY: #5c5c5c;
  --INACTIVE_BROWN: #e86f5466;
  --FADEDBLACK: #111111b3;
  --CORRECTCOLOR: #3cbc81;
  --ERRORCOLOR: #e74a3b;
}

.backdrop {
  opacity: 1;
  z-index: 30;
  background-color: #0a0a0a80;
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  background: var(--WHITE);
  width: 272px;
  padding: 28px 16px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.close_button {
  background-color: #0000;
  border: none;
  width: 18px;
  height: 18px;
  padding: 0;
  position: absolute;
  top: 16px;
  right: 16px;
}

.close_modal_btn {
  width: 18px;
  height: 18px;
}

.contact_us_modal_wraper {
  flex-direction: column;
  display: flex;
}

.contact_us_modal_head {
  font-size: 20px;
  font-weight: 600;
}

.contact_us_modal_txt {
  color: var(--LIGHTGREY);
  margin: 14px 0 18px;
  font-size: 12px;
  font-weight: 500;
}

.contact_us_modal_form_wrapper label:first-child {
  margin-bottom: 14px;
}

.contact_us_modal_form_wrapper label:nth-child(2) {
  margin-bottom: 24px;
}

.contact_us_userdata_modal_label {
  color: var(--LIGHTGREY);
  flex-direction: column;
  justify-content: space-between;
  gap: 8px;
  font-size: 10px;
  font-weight: 500;
  display: flex;
}

.contact_us_modal_input:not(:placeholder-shown):required:valid {
  border-color: var(--CORRECTCOLOR);
  fill: var(--CORRECTCOLOR);
  background-image: url("icons.f8e86957.svg#icon-correct-logo");
  background-position: 100%;
  background-repeat: no-repeat;
  background-size: 20px;
  padding-right: 30px;
}

.contact_us_modal_input:not(:placeholder-shown):required:invalid {
  border-color: var(--ERRORCOLOR);
}

.contact_us_modal_input {
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  align-items: center;
  width: 240px;
  height: 35px;
  padding: 10px 12px;
  font-family: Montserrat;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.contact_us_modal_input:hover {
  border: 1px solid var(--FADEDBLACK);
}

.contact_us_modal_input:focus {
  outline: none;
}

.contact_us_modal_button {
  cursor: pointer;
  color: var(--WHITE);
  background: var(--INACTIVE_BROWN);
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 240px;
  padding: 10px 0;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.contact_us_modal_button.valid {
  background: var(--PINK);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 480px;
    padding: 48px 32px;
  }

  .close_button {
    top: 24px;
    right: 24px;
  }

  .close_modal_btn {
    width: 24px;
    height: 24px;
  }

  .contact_us_modal_head {
    font-size: 32px;
  }

  .contact_us_modal_txt {
    margin: 32px 0;
    font-size: 16px;
  }

  .contact_us_modal_form_wrapper label:first-child {
    margin-bottom: 24px;
  }

  .contact_us_modal_form_wrapper label:nth-child(2) {
    margin-bottom: 38px;
  }

  .contact_us_userdata_modal_label {
    font-size: 14px;
  }

  .contact_us_modal_input {
    width: 416px;
    height: 49px;
    padding: 16px 0 16px 16px;
    font-size: 14px;
  }

  .Contact_us_modal_button {
    width: 416px;
    padding: 14px 0;
    font-size: 18px;
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    width: 480px;
  }
}

